import React, {Component} from 'react';
import './ContentBoxesHomePage.css';
import engineerIconBlack from "../PNGFolder/engineerIconBlack.png";
import engineerIconWhite2 from "../PNGFolder/engineerIconWhite2.png";
import studentLIfeWhite from "../PNGFolder/studentLIfeWhite.png";
import coursesLogoWhite from  "../PNGFolder/coursesLogoWhite.png";
import { Container, Row, Col } from 'react-bootstrap';

export class ContentBoxesHomePage extends Component{
    render(){
        return(
            <Row className="BoxesContainer">
               <Col xs={12} sm={12} md={4} className="BoxOne">
                   <Container className="BoxOneContent">
                        <img src = {engineerIconWhite2} className="boxOneLogo"/>
                        <h3 className="projectTitle">PROJECTS</h3>
                   </Container>
               </Col>
               <Col xs={12} sm={12} md={4} className="BoxTwo">
                   <Container className="BoxTwoContent">
                       <img src= {studentLIfeWhite} className="boxTwoLogo"/>
                       <h3 className="studentLifeTitle">LIFE @ SOE</h3>
                   </Container>
               </Col>
               <Col xs={12} sm={12} md={4} className="BoxThree">
                   <Container className="BoxThreeContent">
                        <img src= {coursesLogoWhite} className="boxThreeLogo"/>
                        <h3 className="coursesTitle">COURSES</h3>
                   </Container>
               </Col>
            </Row>
        )

    }

}

export default ContentBoxesHomePage;